.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.JumboTron {
  background-color: rgba(214, 211, 211, 0.918);
  color: rgb(32, 18, 18);
  margin-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  font-weight: 1000;
  font-size: 24px;
}
.backGround {
  background-image: url("./background.jpg");
  background-size: cover;
}
